.content {
  padding: 0 16px ;

  &__name {
    font-size: 12px;
    line-height: 18px;
    color: #060b35;
    font-weight: 500;
  }

  &__code {
    margin-top: 4px;
    color: #616070;
    font-size: 10px;
    line-height: 14px;
  }

  &__code--position-bottom {
    margin-bottom: 16px;
  }

  .content__rating {
    display: flex;
    align-items: center;
    justify-content: space-between;

  }

  &__rating--position-bottom {
    margin-bottom: 24px;
  }

  &__stars {
    display: flex;
  }

  &__nums {
    color: #060b35;
    font-size: 10px;
    padding-top: 2px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price--position-bottom {
    margin-bottom: 16px;
  }

  &__text {
    color: #616070;
    font-size: 12px;
  }

  &__num {
    font-weight: 700;
    font-size: 16px;
    color: #060b35;
    line-height: 17px;
  }

  &__button {
    background-color: rgb(0, 172, 220);
    border-radius: 5px;
    border: 1px solid rgba(0, 172, 220,0);
    color: white;
    font-family: inherit;
    min-width: 166px;
    height: 40px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
    padding: 0;
    transition: ease 0.7s;
  }
}

.stars {
  width: 16px;
  height: 16px;
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
}

.stars--position-right {
  margin-right: 0;
}

.stars--background-active {
  background-image: url(../images/star-active.svg);
}
